.wrapper {
  display: flex;
  justify-content: flex-end;
  font-size: 1rem;
}

button {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  background: transparent;
  padding: 0;
  font-size: inherit;
  font-family: inherit;
  &.learnmore {
    width: 12em;
    height: auto;
    .circle {
      @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
      position: relative;
      display: block;
      margin: 0;
      width: 3em;
      height: 3em;
      background: $black;
      border-radius: 1.625rem;
      .icon {
        @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        background: $white;
        &.arrow {
          @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
          left: 0.625em;
          width: 1.125em;
          height: 0.125em;
          background: none;
          &::before {
            position: absolute;
            content: '';
            top: -0.25em;
            right: 0.0625em;
            width: 0.625em;
            height: 0.625em;
            border-top: 0.125em solid #fff;
            border-right: 0.125em solid #fff;
            transform: rotate(45deg);
          }
        }
      }
    }
    .buttontext {
      @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 0.75em 0;
      margin: 0 0 0 1.85em;
      color: $black;
      font-weight: 700;
      line-height: 1.6;
      text-align: center;
      text-transform: uppercase;
    }
  }
  &:hover {
    .circle {
      width: 100%;
      .icon {
        &.arrow {
          background: $white;
          transform: translate(1em, 0);
        }
      }
    }
    .buttontext {
      color: $white;
    }
  }
}

